import React, { useState, useMemo, useEffect } from 'react';
import Select from 'react-select';
import useFetchData from '../hooks/useFetchData';
import { useAuth } from '../context/AuthContext';
import './ProductsPage.css';
import TabSlider from '../components/products/TabSlider';
import duraflexLogo from '../assets/images/duraflex-logo.png';
import { getProductImage } from '../utils/imageLoader';

const PRODUCTS_URL = 'https://raw.githubusercontent.com/nguyenthong123/dashboard-data/main/data/products.json';
const PRICES_URL = 'https://raw.githubusercontent.com/nguyenthong123/dashboard-data/main/data/bang_gia_dai_ly.json';
const ADVANCED_ROLES = ['Ca Hng', 'Nh My Tn', 'ad mind'];
const SPECIAL_PRICE_TYPES = ['gi nim yt', 'Gi Thu Th', 'Gi ch nh'];

// Style ty chnh cho react-select
const customSelectStyles = {
  control: (provided) => ({
    ...provided,
    backgroundColor: 'var(--background-color)',
    borderColor: 'var(--border-color)',
    minHeight: '42px',
    color: 'var(--text-color)',
  }),
  valueContainer: (provided) => ({ ...provided, padding: '0 8px' }),
  input: (provided) => ({ ...provided, color: 'var(--text-color)' }),
  multiValue: (provided) => ({ ...provided, backgroundColor: '#e9ecef' }),
  multiValueLabel: (provided) => ({ ...provided, color: '#495057' }),
  menu: (provided) => ({ ...provided, backgroundColor: 'var(--card-background)', zIndex: 5 }),
  option: (provided, state) => ({
    ...provided,
    backgroundColor: state.isSelected ? 'var(--primary-green)' : state.isFocused ? 'var(--background-color)' : 'transparent',
    color: state.isSelected ? 'white' : 'var(--text-color)',
  }),
  placeholder: (provided) => ({ ...provided, color: 'var(--text-secondary-color)' }),
  singleValue: (provided) => ({ ...provided, color: 'var(--text-color)' }),
};


function ProductsPage() {
  const { user } = useAuth();
  const { data: products, isLoading: isLoadingProducts } = useFetchData(PRODUCTS_URL);
  const { data: prices, isLoading: isLoadingPrices } = useFetchData(PRICES_URL);
  
  const [selectedPriceType, setSelectedPriceType] = useState('gi nim yt');
  const [selectedGroups, setSelectedGroups] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');

  const availablePriceTypes = useMemo(() => {
    if (!prices) return [];
    const priceKeys = new Set();
    const excludedKeys = ['group_id', 'id_san_pham', 'Tn sn phm', 'kch thc', 's kg trn tm', 's tm /kin', 'image sn phm', 'width_mm', 'length_mm', 'product_slug', 'long_description', 'youtube_id', 'warranty_id'];
    prices.forEach(price => {
      Object.keys(price).forEach(key => {
        if (!excludedKeys.includes(key) && price[key] !== "" && price[key] !== null && price[key] !== 0) {
          priceKeys.add(key);
        }
      });
    });
    return Array.from(priceKeys).sort();
  }, [prices]);

  useEffect(() => {
    if (!availablePriceTypes.length) return;
    if (user?.phan_loai === 'Thu Th') {
      setSelectedPriceType('Gi ch nh');
    } else if (ADVANCED_ROLES.includes(user?.phan_loai)) {
      const defaultPrice = availablePriceTypes.find(p => p === 'gi nim yt') || availablePriceTypes[0];
      setSelectedPriceType(defaultPrice);
    }
  }, [user, availablePriceTypes]);

  const groupOptions = useMemo(() => {
    if (!products) return [];
    return Object.values(products).map(p => ({ value: p.name, label: p.name }));
  }, [products]);

  const filteredAndGroupedData = useMemo(() => {
    if (!products || !prices) return [];
    let currentPrices = [...prices];
    if (searchTerm) {
      currentPrices = currentPrices.filter(p => p["Tn sn phm"] && p["Tn sn phm"].toLowerCase().includes(searchTerm.toLowerCase()));
    }
    // Keep variants that have the currently selected price type OR any available price type.
    currentPrices = currentPrices.filter(p => {
      if (p[selectedPriceType]) return true;
      // Fallback: if the variant has any of the other available price keys with a value, include it.
      for (const key of availablePriceTypes) {
        if (p[key]) return true;
      }
      return false;
    });
    const grouped = currentPrices.reduce((acc, variant) => {
      // Try to find matching group in `products` using the variant.group_id.
      // Some data sources use underscores while `products.json` uses hyphens.
      let groupKey = variant.group_id;
      let groupInfo = products[groupKey];
      if (!groupInfo && typeof groupKey === 'string') {
        const altKey = groupKey.replace(/_/g, '-');
        groupInfo = products[altKey];
        groupKey = altKey;
      }
      if (!groupInfo) return acc;

      const groupName = groupInfo.name || groupKey;
      acc[groupName] = acc[groupName] || { ...groupInfo, id: groupInfo.id || groupKey, name: groupName, variants: [] };
      acc[groupName].variants.push(variant);
      return acc;
    }, {});
    
    let result = Object.values(grouped);
    if (selectedGroups.length > 0) {
      const selectedGroupNames = selectedGroups.map(g => g.value);
      result = result.filter(group => selectedGroupNames.includes(group.name));
    }
    return result;
  }, [products, prices, selectedPriceType, selectedGroups, searchTerm, availablePriceTypes]);

  if (isLoadingProducts || isLoadingPrices) {
    return <div className="page-container">Loading product details...</div>;
  }
  
  const shouldShowNotes = SPECIAL_PRICE_TYPES.includes(selectedPriceType);

  return (
    <>
      <div className="page-container">
        <h1>Bng Gi Sn Phm</h1>
        <p>Hin th gi theo vai tr ca bn: <strong>{user?.phan_loai}</strong></p>

        <div className="price-filters">
          <div className="filter-group">
            <label>Lc theo Nhm sn phm:</label>
            <Select
              isMulti
              options={groupOptions}
              value={selectedGroups}
              onChange={setSelectedGroups}
              placeholder="Chn mt hoc nhiu nhm..."
              styles={customSelectStyles} 
            />
          </div>
          <div className="filter-group">
            <label>Tm kim Tn sn phm:</label>
            <input 
              type="text" 
              placeholder="Nhp tn sn phm,  dy..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>
        </div>

        <div className="price-selector-container">
          {user?.phan_loai === 'Thu Th' ? (
            <>
              <button className={`price-selector-button ${selectedPriceType === 'Gi ch nh' ? 'active' : ''}`} onClick={() => setSelectedPriceType('Gi ch nh')}>Gi ch nh</button>
              <button className={`price-selector-button ${selectedPriceType === 'Gi Thu Th' ? 'active' : ''}`} onClick={() => setSelectedPriceType('Gi Thu Th')}>Gi Thu Th</button>
            </>
          ) : ADVANCED_ROLES.includes(user?.phan_loai) ? availablePriceTypes.map(priceType => (
            <button key={priceType} className={`price-selector-button ${selectedPriceType === priceType ? 'active' : ''}`} onClick={() => setSelectedPriceType(priceType)}>{priceType}</button>
          )) : null }
        </div>

        {filteredAndGroupedData.length > 0 ? (
          filteredAndGroupedData.map(product => (
            <div key={product.id} className="product-group">
              <div className="product-group-title">
                {product.id.includes('duraflex') && <img src={duraflexLogo} alt="DURAfex Logo" className="logo" />}
                <h2>{product.name}</h2>
              </div>
              <div className="table-container">
                <table className="product-price-table">
                  <thead>
                    <tr>
                      <th style={{ width: '80px' }}>Hnh nh</th>
                      <th>Tn sn phm</th>
                      <th>Kch thc</th>
                      <th>S kg / tm</th>
                      <th>S tm / kin</th>
                      <th>Gi ({selectedPriceType})</th>
                    </tr>
                  </thead>
                  <tbody>
                    {product.variants.map(variant => {
                      // *** LOGIC LY NH U TIN  C THM VO Y ***
                      const imageFileNamesString = variant["image sn phm"] || '';
                      const firstImageName = imageFileNamesString.split(',')[0].trim();
                      const imageSrc = getProductImage(firstImageName);

                      return (
                        <tr key={variant.id_san_pham}>
                          <td className="product-image-cell">
                            <img 
                              src={imageSrc} 
                              alt={variant["Tn sn phm"]} 
                              className="product-image" 
                              onError={(e) => { e.target.onerror = null; e.target.src="https://via.placeholder.com/60x40?text=No+Img"; }}
                            />
                          </td>
                          <td>{variant["Tn sn phm"]}</td>
                          <td>{variant["kch thc"]}</td>
                          <td>{variant["s kg trn tm"]}</td>
                          <td>{variant["s tm /kin"]}</td>
                          <td className="price-cell">
                            {variant[selectedPriceType]?.toLocaleString('vi-VN') || ''} VN
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
              </div>
            </div>
          ))
        ) : (
          <div style={{ marginTop: '3rem', textAlign: 'center', color: 'var(--text-secondary-color)' }}>
            <p>Khng c sn phm no ph hp vi b lc hin ti.</p>
          </div>
        )}

        {shouldShowNotes && (
          <div className="notes-section">
            <h3>Lu :</h3>
            <ul>
              <li>Min ph vn chuyn vi tng n hng &gt;= 5,5 tr ; n hng di 5,5 tr ph vn chuyn cn cng thm 250 ngn/ n .</li>
              <li>n gi  c s chp thun ca cc i l phn phi ( Hong Sa Phc An , Ph Cng C M'gar, Ngc Linh C kuin,Xun Qunh Tuy c ,Trng Th tp.bmt.,nmt Bun Ma Thut ).</li>
              <li>Sn phm lun c QR chnh hng c dn trn tm.</li>
              <li> 2024 - DURAfex Vinh Tng. Mi quyn c bo lu.</li>
            </ul>
          </div>
        )}
      </div>
      <TabSlider />
    </>
  );
}

export default ProductsPage;