import React from 'react';
import { Link } from 'react-router-dom';
import './Breadcrumbs.css';

// Component ny nhn vo mt mng cc "mu bnh m"
// Mi mu l mt object { label: 'Tn hin th', link: '/duong-dan' }
// Mu cui cng s khng c link
function Breadcrumbs({ crumbs }) {
  if (!crumbs || crumbs.length === 0) {
    return null;
  }

  return (
    <nav aria-label="breadcrumb">
      <ol className="breadcrumbs">
        {crumbs.map((crumb, index) => (
          <li key={index} className={`breadcrumbs-item ${index === crumbs.length - 1 ? 'active' : ''}`}>
            {index < crumbs.length - 1 ? (
              // Nu khng phi l mu cui cng, hin th Link
              <Link to={crumb.link}>{crumb.label}</Link>
            ) : (
              // Nu l mu cui cng, ch hin th text
              <span>{crumb.label}</span>
            )}
            {/* Hin th du phn cch, tr mu cui cng */}
            {index < crumbs.length - 1 && <span className="breadcrumbs-separator">&gt;</span>}
          </li>
        ))}
      </ol>
    </nav>
  );
}

export default Breadcrumbs;