import React, { useState, useMemo, useEffect } from 'react';
import useFetchData from '../hooks/useFetchData';
import { useAuth } from '../context/AuthContext';
import './ProductsPage.css';
import './DashboardPage.css';

const ORDERS_URL = 'https://raw.githubusercontent.com/nguyenthong123/dashboard-data/main/data/orderData.json';
const ORDER_DETAILS_URL = 'https://raw.githubusercontent.com/nguyenthong123/dashboard-data/main/data/orderDetails.json';

// --- COMPONENT CON: MODAL CHI TIT N HNG (Sao chp t DashboardPage) ---
function OrderDetailsModal({ order, details, onClose }) {
  const modalOverlayStyle = {
    position: 'fixed', top: 0, left: 0, width: '100vw', height: '100vh',
    backgroundColor: 'rgba(0, 0, 0, 0.6)', display: 'flex',
    justifyContent: 'center', alignItems: 'center', zIndex: 1050,
  };
  const modalContentStyle = {
    position: 'relative', backgroundColor: 'var(--card-background)', color: 'var(--text-color)', 
    padding: '2rem', borderRadius: '8px', width: '90%', maxWidth: '960px', 
    maxHeight: '90vh', overflowY: 'auto', boxShadow: '0 5px 15px rgba(0,0,0,0.3)',
  };
  const closeButtonStyle = {
    position: 'absolute', top: '10px', right: '15px', background: 'none',
    border: 'none', fontSize: '1.8rem', cursor: 'pointer', color: 'var(--text-secondary-color)',
  };

  return (
    <div style={modalOverlayStyle} onClick={onClose}>
      <div style={modalContentStyle} onClick={(e) => e.stopPropagation()}>
        <button style={closeButtonStyle} onClick={onClose}>&times;</button>
        <h2>Chi tit n hng: {order["id order"]}</h2>
        <p><strong>Khch hng:</strong> {order["tn khch hng"]}</p>
        <p><strong>Ngy t:</strong> {order["thi gian ln n"] || ''}</p>
        <h4 style={{marginTop: '2rem'}}>Cc sn phm trong n:</h4>
        <div className="table-container">
          <table className="product-price-table" style={{ tableLayout: 'auto' }}>
            <thead>
              <tr>
                <th>Tn sn phm</th>
                <th>Kch Thc</th>
                <th>S lng</th>
                <th>n gi</th>
                <th>Thnh tin</th>
              </tr>
            </thead>
            <tbody>
              {details.length > 0 ? (
                details.map((item, index) => {
                  const price = parseFloat(item["gi tin"]) || 0;
                  const total = parseFloat(item["tng tin"]) || 0;
                  return (
                    <tr key={item["id order chi tit"] || index}>
                      <td style={{ whiteSpace: 'nowrap' }}>{item["tn sn phm"] || item["tn kha kt hp sp"] || ''}</td>
                      <td style={{ whiteSpace: 'nowrap' }}>{item["Kch Thc"] || ''}</td>
                      <td style={{ whiteSpace: 'nowrap' }}>{item["s lng"]} {item["n v tnh"]}</td>
                      <td style={{ textAlign: 'right', whiteSpace: 'nowrap' }}>{price.toLocaleString('vi-VN')}</td>
                      <td style={{ textAlign: 'right', whiteSpace: 'nowrap' }}>{total.toLocaleString('vi-VN')}</td>
                    </tr>
                  )
                })
              ) : ( <tr><td colSpan="5" style={{ textAlign: 'center' }}>Khng tm thy chi tit cho n hng ny.</td></tr> )}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}

// --- COMPONENT CON: PHN TRANG (Sao chp t DashboardPage) ---
function Pagination({ itemsPerPage, totalItems, paginate, currentPage }) {
  const pageNumbers = [];
  for (let i = 1; i <= Math.ceil(totalItems / itemsPerPage); i++) {
    pageNumbers.push(i);
  }
  const paginationStyle = { display: 'flex', justifyContent: 'center', listStyle: 'none', padding: 0, marginTop: '2rem' };
  const baseLinkStyle = {
    color: 'var(--primary-blue)', padding: '8px 16px', textDecoration: 'none', 
    margin: '0 4px', borderRadius: '4px', borderWidth: '1px',
    borderStyle: 'solid', borderColor: 'var(--border-color)', transition: 'all 0.2s',
    backgroundColor: 'var(--card-background)'
  };
  const activePageLinkStyle = { 
    backgroundColor: 'var(--primary-blue)', color: 'white', borderColor: 'var(--primary-blue)'
  };
  return (
    <nav>
      <ul style={paginationStyle}>
        {pageNumbers.map(number => (
          <li key={number}>
            <a onClick={(e) => { e.preventDefault(); paginate(number); }} href="!#" style={currentPage === number ? { ...baseLinkStyle, ...activePageLinkStyle } : baseLinkStyle}>
              {number}
            </a>
          </li>
        ))}
      </ul>
    </nav>
  );
}

// --- COMPONENT CHNH ---
function CustomerRevenuePage() {
  const { user } = useAuth();
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage] = useState(10);
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [statusFilter, setStatusFilter] = useState('Tt c');

  const { data: rawOrders, isLoading: isLoadingOrders } = useFetchData(ORDERS_URL);
  const { data: rawOrderDetails, isLoading: isLoadingDetails } = useFetchData(ORDER_DETAILS_URL);

  useEffect(() => {
    setCurrentPage(1);
  }, [startDate, endDate, statusFilter]);

  const allOrders = useMemo(() => {
    if (!rawOrders) return [];
    return Array.isArray(rawOrders) ? rawOrders : Object.values(rawOrders);
  }, [rawOrders]);

  const customerOrders = useMemo(() => {
    if (!user?.name || allOrders.length === 0) return [];
    
    // Chun ha tn khch hng (b khong trng tha, chuyn v ch thng)
    const normalizedUserName = user.name.toLowerCase().trim().replace(/\s+/g, ' ');
    
    return allOrders.filter(order => {
      if (!order["tn khch hng"]) return false;
      // Chun ha tn trong n hng
      const normalizedOrderCustomer = order["tn khch hng"].toLowerCase().trim().replace(/\s+/g, ' ');
      return normalizedOrderCustomer === normalizedUserName;
    });
  }, [allOrders, user]);

  const availableStatuses = useMemo(() => {
    const statuses = new Set(customerOrders.map(order => order["trng thi"]).filter(Boolean));
    return ['Tt c', ...statuses];
  }, [customerOrders]);

  const filteredOrders = useMemo(() => {
    let filtered = [...customerOrders];
    if (startDate) {
      const start = new Date(startDate);
      start.setHours(0, 0, 0, 0);
      filtered = filtered.filter(order => {
        if (!order["thi gian ln n"]) return false;
        const [day, month, year] = order["thi gian ln n"].split('/');
        const orderDate = new Date(year, month - 1, day);
        return orderDate >= start;
      });
    }
    if (endDate) {
      const end = new Date(endDate);
      end.setHours(23, 59, 59, 999);
      filtered = filtered.filter(order => {
        if (!order["thi gian ln n"]) return false;
        const [day, month, year] = order["thi gian ln n"].split('/');
        const orderDate = new Date(year, month - 1, day);
        return orderDate <= end;
      });
    }
    if (statusFilter && statusFilter !== 'Tt c') {
      filtered = filtered.filter(order => order["trng thi"] === statusFilter);
    }
    return filtered;
  }, [customerOrders, startDate, endDate, statusFilter]);

  const selectedOrderDetails = useMemo(() => {
    if (!selectedOrder || !rawOrderDetails) return [];
    return rawOrderDetails[selectedOrder["id order"]] || [];
  }, [selectedOrder, rawOrderDetails]);

  if (isLoadingOrders || isLoadingDetails) {
    return <div className="page-container">ang ti d liu...</div>;
  }
  
  // Dng `filteredOrders` (mt mng chc chn tn ti)  tnh ton
  const totalRevenue = filteredOrders.reduce((sum, order) => sum + (parseFloat(order["tng ph"]) || 0), 0);
  const totalPackages = filteredOrders.reduce((sum, order) => sum + (parseInt(order["tng kin trong n"], 10) || 0), 0);
  
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentOrders = filteredOrders.slice(indexOfFirstItem, indexOfLastItem);
  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  return (
    <div className="page-container">
      <h1>Doanh thu ca: {user?.name}</h1>
      
      <div className="filter-container">
        <input type="date" value={startDate} onChange={e => setStartDate(e.target.value)} title="T ngy" />
        <input type="date" value={endDate} onChange={e => setEndDate(e.target.value)} title="n ngy" />
        <select value={statusFilter} onChange={e => setStatusFilter(e.target.value)}>
          {availableStatuses.map(status => <option key={status} value={status}>{status || 'Cha c'}</option>)}
        </select>
        <input 
          type="text" 
          placeholder="Tn khch hng"
          value={user?.name || ''} 
          disabled 
        />
      </div>

      <div className="stats-container">
        <div className="stat-card"><h3>Tng s n hng</h3><p>{filteredOrders.length}</p></div>
        <div className="stat-card"><h3>Tng doanh thu</h3><p>{totalRevenue.toLocaleString('vi-VN')} VN</p></div>
        <div className="stat-card"><h3>Tng kin</h3><p>{totalPackages}</p></div>
      </div>

      <h2>Danh sch n hng</h2>
      <div className="table-container">
        <table className="product-price-table">
          <thead>
            <tr>
              <th>Thi gian ln n</th>
              <th>M n Hng</th>
              <th>Trng Thi</th>
              <th>Tng ph</th>
              <th>S tin cn li</th>
              <th>Tng kin</th>
            </tr>
          </thead>
          <tbody>
            {currentOrders.map((order, index) => {
              const subTotal = parseFloat(order["tng ph"]);
              const remainingAmount = parseFloat(order["s tin cn li trong n"]);
              return (
                <tr key={order["id order"] || index} onClick={() => setSelectedOrder(order)} style={{ cursor: 'pointer' }}>
                  <td>{order["thi gian ln n"] ? order["thi gian ln n"].split('/').reverse().join('-') : ''}</td>
                  <td>{order["id order"]}</td>
                  <td>{order["trng thi"] || ''}</td>
                  <td style={{ textAlign: 'right' }}>{subTotal > 0 ? `${subTotal.toLocaleString('vi-VN')} VN` : ''}</td>
                  <td style={{ textAlign: 'right' }}>{remainingAmount > 0 ? `${remainingAmount.toLocaleString('vi-VN')} VN` : ''}</td>
                  <td>{order["tng kin trong n"] || ''}</td>
                </tr>
              )
            })}
          </tbody>
        </table>
      </div>

      <Pagination itemsPerPage={itemsPerPage} totalItems={filteredOrders.length} paginate={paginate} currentPage={currentPage} />
      
      {selectedOrder && (
        <OrderDetailsModal 
          order={selectedOrder} 
          details={selectedOrderDetails}
          onClose={() => setSelectedOrder(null)} 
        />
      )}
    </div>
  );
}

export default CustomerRevenuePage;