import React from 'react';
import { Link } from 'react-router-dom';
import './FeaturedProducts.css';
import { getProductImage } from '../../utils/imageLoader';

function FeaturedProducts({ prices }) {
  if (!prices || prices.length === 0) {
    return null;
  }

  return (
    <div className="featured-products-container">
      {prices.map(item => {
        // --- THAY I CHNH  Y ---
        
        // 1. Ly ton b chui tn file nh t JSON
        const imageFileNamesString = item["image sn phm"] || '';
        
        // 2. Tch chui thnh mng v ly ra phn t u tin
        const firstImageName = imageFileNamesString.split(',')[0].trim();
        
        // 3. Dng tn file u tin   ly nh
        const imageSrc = getProductImage(firstImageName);

        if (!item.product_slug) {
          return null;
        }

        return (
          <div key={item.id_san_pham} className="product-card">
            <div className="product-card-image-wrapper">
              <img 
                src={imageSrc} 
                alt={item["Tn sn phm"]} 
                className="product-card-image"
                onError={(e) => { e.target.onerror = null; e.target.src="https://via.placeholder.com/280x180?text=No+Image"; }}
              />
            </div>
            <div className="product-card-content">
              <h3 className="product-card-title">{item["Tn sn phm"]}</h3>
              <p className="product-card-info">Kch thc: {item["kch thc"] || 'N/A'}</p>
              <p className="product-card-info">S kg/tm: {item["s kg trn tm"] || 'N/A'}</p>
              <p className="product-card-price">
                Gi t: {item["Gi ch nh"]?.toLocaleString('vi-VN') || 'Lin h'} VN
              </p>
              <Link to={`/san-pham/${item.product_slug}`} className="product-card-button">
                Chi tit
              </Link>
            </div>
          </div>
        );
      })}
    </div>
  );
}

export default FeaturedProducts;