import React from 'react';
import { Outlet } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer'; // <-- 1. Import Footer
import AdminFab from '../common/AdminFab';
import { useAuth } from '../../context/AuthContext';

const FAB_VIEWER_ROLES = ['Ca Hng', 'Nh My Tn', 'ad mind'];

function MainLayout() {
  const { user } = useAuth();
  const canViewFab = user && FAB_VIEWER_ROLES.includes(user.phan_loai);

  return (
    <div style={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
      <Header />
      
      {/* Thm flexGrow: 1  y footer xung cui trang */}
      <main style={{ flexGrow: 1 }}>
        <Outlet />
      </main>
      
      {canViewFab && <AdminFab />}
      
      {/* 2. Thm Footer vo y */}
      <Footer />
    </div>
  );
}

export default MainLayout;